import React from "react";
import {Button, FormControl, InputGroup, Table, Dropdown, DropdownButton} from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';

const TableBody = ({rows}) => {
    return (
        <div className="Table">
            <h1>Таблица</h1>
            <InputGroup className="mb-3">
                <DropdownButton
                    as={InputGroup.Prepend}
                    variant="outline-secondary"
                    title="Колонка"
                    id="input-group-dropdown-1"
                >
                    <Dropdown.Item href="#">Название</Dropdown.Item>
                    <Dropdown.Item href="#">Количество</Dropdown.Item>
                    <Dropdown.Item href="#">Расстояние</Dropdown.Item>
                </DropdownButton>
                <DropdownButton
                    as={InputGroup.Prepend}
                    variant="outline-secondary"
                    title="Условие"
                    id="input-group-dropdown-2"
                >
                    <Dropdown.Item href="#">Больше</Dropdown.Item>
                    <Dropdown.Item href="#">Меньше</Dropdown.Item>
                    <Dropdown.Item href="#">Равно</Dropdown.Item>
                    <Dropdown.Item href="#">Содержит</Dropdown.Item>
                </DropdownButton>
                <FormControl
                    aria-describedby="basic-addon2"
                />
                <InputGroup.Append>
                    <Button variant="outline-secondary">Найти</Button>
                </InputGroup.Append>
            </InputGroup>
            <Table striped bordered variant="dark" className="mt-3">
                <thead>
                <tr>
                    <th>Дата</th>
                    <th>Название</th>
                    <th>Количество</th>
                    <th>Расстояние</th>
                </tr>
                </thead>
                <tbody>
                {rows.results.length !== 0 ? rows.results.map(row => (
                    <tr key={row.id}>
                        <td>{row.date}</td>
                        <td>{row.title}</td>
                        <td>{row.count}</td>
                        <td>{row.distance}</td>
                    </tr>
                )) : <h4>Данные не найдены.</h4>}
                </tbody>
            </Table>
        </div>
    );
}

export default TableBody;
